.page-header {
  position: fixed;
  z-index: 5;
  top: 0;
  left: 0;
  width: 100%;

  background-color: var(--color-bg-darker);
  border-bottom: 1px var(--color-primary) solid;

  & .wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px;
  }
}
