.code-example {
  padding-top: 40px;
}

.javascript {
  display: flex;
  justify-content: center;
  margin: 0;
  padding: 0 20px 0 20px;

  font-size: 1.2rem;
  white-space: pre-wrap;

  background: linear-gradient(225deg, var(--color-bg-darker) 31%, var(--color-gradient));
  box-shadow: 0px 5px 10px 2px rgb(34 60 80 / 20%);

  @include media-tablet {
    font-size: 2rem;
  }
}
